import React from 'react';
import { Button } from '@material-ui/core';

const PrintComplete = ({ setStatus, setActiveMode }) => {
  return (
    <>
      <Button
        color="primary"
        variant="contained"
        onClick={() => setStatus(0)}
        size="large"
      >
        Print Again
      </Button>
      <Button
        color="secondary"
        variant="contained"
        size="large"
        onClick={() => setActiveMode(null)}
        style={{ marginTop: '12px' }}
      >
        Exit
      </Button>
    </>
  );
};

export default PrintComplete;
