import React from 'react';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Button,
} from '@material-ui/core';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import Thermometer from './thermometer';
import './Heating.css';

const Heating = ({ bed, extruder }) => {
  let sendGcode = function (gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        gcode: gcode,
      }),
    });
  };

  return (
    <Accordion>
      <AccordionSummary classes={{ content: 'accordion-style' }}>
        <h3 style={{ margin: 0 }}>Heating</h3>
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            sendGcode('M104 S0\nM140 S0\n');
          }}
        >
          Cooldown All
        </Button>
        <ArrowForwardIosIcon />
      </AccordionSummary>
      <AccordionDetails>
        <div className="control-heating-container">
          <h4>Nozzle</h4>
          <div>
            <Button
              variant="outlined"
              onClick={() => {
                sendGcode('M104 S220\n');
              }}
            >
              Preheat PLA
            </Button>
            <Button
              variant="outlined"
              onClick={() => {
                sendGcode('M104 S0\n');
              }}
            >
              Cooldown
            </Button>
          </div>
          <h4>Bed</h4>
          <div style={{ display: 'flex' }}>
            <Button
              variant="outlined"
              onClick={() => {
                sendGcode('M140 S50\n');
              }}
            >
              Preheat PLA
            </Button>
            <Button
              variant="outlined"
              onClick={() => {
                sendGcode('M140 S0\n');
              }}
            >
              Cooldown
            </Button>
          </div>
        </div>
        <div className="monitor-heating-container">
          <div className="Guages">
            <div>
              <Thermometer
                label="Nozzle"
                value={extruder}
                min={0}
                max={400}
              ></Thermometer>
              {/*<input placeholder="N/A"></input>*/}
            </div>
            <div>
              <Thermometer
                label="Bed"
                value={bed}
                min={0}
                max={150}
              ></Thermometer>
              {/*<input placeholder="N/A"></input>*/}
            </div>
          </div>
        </div>
      </AccordionDetails>
    </Accordion>
  );
};

export default Heating;
