import React, { useState } from 'react';
import { Snackbar } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import PrinterType from './PrinterType/PrinterType';
import NetworkType from './NetworkType/NetworkType';
import UserLevel from './UserLevel/UserLevel';

const useStyles = makeStyles({
  container: {
    textAlign: 'center',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
  },
});
const Setup = ({ setActiveMode }) => {
  const classes = useStyles();

  const [step, setStep] = useState(0);
  const [showSnack, setShowSnack] = useState(false);
  const [snackMessage, setSnackMessage] = useState('');
  const handleSnackClose = () => {
    setShowSnack(() => false);
    setSnackMessage('');
  };
  return (
    <div className={classes.container}>
      {step === 0 && <PrinterType setStep={setStep} />}
      {step === 1 && (
        <NetworkType
          setStep={setStep}
          setShowSnack={setShowSnack}
          setSnackMessage={setSnackMessage}
          handleSnackClose={handleSnackClose}
        />
      )}
      {step === 2 && <UserLevel setActiveMode={setActiveMode} />}
      <Snackbar
        open={showSnack}
        autoHideDuration={6000}
        message={snackMessage}
        onClose={handleSnackClose}
      ></Snackbar>
    </div>
  );
};

export default Setup;
