import React from "react";

import "./style.css";

export default function Thermometer(props) {
  return (
    <div className="Thermometer">
      {props.label}
      <div className="Thermometer-Container">
        <div className="Thermometer-Bulb-Container"></div>
        <div className="Thermometer-Indicator-Container">
          <div
            className="Thermometer-Indicator"
            style={{
              height: `${25 +
                100 * ((props.value - props.min) / (props.max - props.min))}%`
            }}
          ></div>
          <div className="Thermometer-Bulb">
            <span>{props.value}</span>
          </div>
        </div>
      </div>
    </div>
  );
}
