import React, { useRef, useState } from 'react';
import { useMountEffect } from '../../../../Utility/useMountEffect';
import { makeStyles } from '@material-ui/core/styles';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Backdrop,
  Button,
  CircularProgress,
  IconButton,
  List,
  ListItem,
  TextField,
  Snackbar,
} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';

const useStyles = makeStyles({
  accordion: {
    display: 'flex',
    justifyContent: 'center',
  },
  backdrop: {
    zIndex: 10000,
    color: '#fff',
  },
  button: {
    alignSelf: 'flex-end',
    height: '36px',
    marginLeft: '25px',
  },
  connecting: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    '& h4': {
      marginBottom: '24px',
    },
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
  },
  item: {
    padding: '30px 15px',
    fontSize: '24px',
    display: 'flex',
    justifyContent: 'space-between',
  },
  keyboard: {
    position: 'fixed',
    bottom: 0,
    left: 0,
    right: 0,
    color: '#000',
  },
});
const WifiConnect = ({
  setStep,
  setShowSnack,
  setSnackMessage,
  handleSnackClose,
  setConnectWifi,
}) => {
  const classes = useStyles();

  const [expanded, setExpanded] = useState(false);
  const [layout, setLayout] = useState('default');
  const [input, setInput] = useState('');
  const [wifiArr, setWifiArr] = useState([]);
  const [connecting, setConnecting] = useState(false);

  const keyboard = useRef();

  useMountEffect(() => {
    async function getNetworks() {
      // Default options are marked with *
      const response = await fetch(`http://makerhub.local:8990/networks/scan`);
      return response.json(); // parses JSON response into native JavaScript objects
    }
    //Fetch http://makerhub.local:8990/networks/scan
    //setWifiArr response.networks
    getNetworks().then(data => {
      let networks = data.networks.filter((obj, pos, arr) => {
          return arr.map(
            mapObj => mapObj['ssid']
          ).indexOf(obj['ssid']) === pos && obj.ssid != null;
      });
      setWifiArr(networks);
    });
  });

  const handleSubmit = async () => {
    const body = {ssid: expanded,password: input};
    //set loading screen
    setExpanded(false);
    setConnecting(true);
    setInput('');
    //await POST body to http://makerhub.local:8990/connect
    async function setNetwork(body) {
      console.log(body);
      // Default options are marked with *
      const response = await fetch(
        `http://makerhub.local:8990/connect`,
        {
          method: "POST",
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(body)
        }
      );
      return response.json(); // parses JSON response into native JavaScript objects
    }
    setNetwork(body).then(data => {
      //after, display success or failure
      setConnecting(false);
      if (data.status == "connected!") {
        setSnackMessage('Successfully Connected');
        //Go to next screen
        setStep(2);
      } else {
        setSnackMessage('Connection Failed');
        // show snack message "Failure"
      }
      setShowSnack(() => true);
    });

  };
  const onChange = (input) => {
    setInput(input);
    console.log('Input changed', input);
  };
  const handleShift = () => {
    const newLayoutName = layout === 'default' ? 'shift' : 'default';
    setLayout(newLayoutName);
  };
  const onKeyPress = (button) => {
    /**
     * If you want to handle the shift and caps lock buttons
     */
    (button === '{shift}' || button === '{lock}') && handleShift();
    button === '{enter}' && handleSubmit();
  };
  const handleExpand = (panel) => {
    setExpanded(panel);
  };
  const onChangeInput = (event) => {
    const input = event.target.value;
    setInput(input);
    keyboard.current.setInput(input);
  };

  return (
    <div className={classes.container}>
      <div className={classes.header}>
        <IconButton
          aria-label="back"
          onClick={() => setConnectWifi(() => null)}
        >
          <ArrowBackIcon />
        </IconButton>
        <h2>Wi-Fi</h2>
      </div>

      <List>
        {wifiArr.map((network) => {
          return (
            <Accordion
              expanded={expanded === network.ssid}
              key={`${network.ssid}-list-item`}
            >
              <AccordionSummary classes={{ content: 'accordion-style' }}>
                <ListItem className={classes.item}>
                  {network.ssid}
                  <Button
                    color={expanded === network.ssid ? 'secondary' : 'primary'}
                    variant="contained"
                    onClick={() => {
                      if (expanded === network.ssid) {
                        setExpanded(() => false);
                        setInput('');
                      } else {
                        handleExpand(network.ssid);
                      }
                    }}
                  >
                    {expanded === network.ssid ? 'Close' : 'Join'}
                  </Button>
                </ListItem>
              </AccordionSummary>
              <AccordionDetails className={classes.accordion}>
                <TextField
                  id="standard-password-input"
                  label="Password"
                  type="password"
                  color="secondary"
                  value={input}
                  onChange={onChangeInput}
                />
                <Button
                  color="primary"
                  variant="contained"
                  className={classes.button}
                  onClick={() => {
                    if (input.length > 0) {
                      handleSubmit();
                    } else {
                      setSnackMessage('Password required');
                      setShowSnack(true);
                    }
                  }}
                >
                  Connect
                </Button>
              </AccordionDetails>
            </Accordion>
          );
        })}
      </List>
      {connecting && (
        <Backdrop className={classes.backdrop} open={connecting}>
          <div className={classes.connecting}>
            <h4>Connecting</h4>
            <CircularProgress color="inherit" />
          </div>
        </Backdrop>
      )}
      {expanded && (
        <div className={classes.keyboard}>
          <Keyboard
            keyboardRef={(r) => (keyboard.current = r)}
            layoutName={layout}
            onChange={onChange}
            onKeyPress={onKeyPress}
          />
        </div>
      )}
    </div>
  );
};

export default WifiConnect;
