import React from 'react';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Button,
  ButtonGroup,
} from '@material-ui/core';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';

import './Moving.css';

const Moving = ({ position }) => {
  let sendGcode = function (gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        gcode: gcode,
      }),
    });
  };

  return (
    <Accordion>
      <AccordionSummary classes={{ content: 'accordion-style' }}>
        <h3 style={{ margin: '0' }}>Moving</h3>
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            sendGcode('G28\nM114\n');
          }}
        >
          Home All <FontAwesomeIcon icon={faHome} />
        </Button>
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            sendGcode('G29\nM114\n');
          }}
        >
          Level Bed <FontAwesomeIcon />
        </Button>
        <ArrowForwardIosIcon />
      </AccordionSummary>
      <AccordionDetails>
        <div className="control-moving-container">
          <div className="control-moving-sub-container">
            <div className="control-moving-sub-header">
              <h4>X</h4>
              <input
                readOnly
                value={position.x ? position.x : 0 + 'mm'}
              ></input>
            </div>
            <ButtonGroup aria-label="outlined primary button group">
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X-10\nM114\n');
                }}
              >
                -10
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X-1\nM114\n');
                }}
              >
                -1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X-0.1\nM114\n');
                }}
              >
                -0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G28 X\nM114\n');
                }}
              >
                <FontAwesomeIcon icon={faHome} />
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X0.1\nM114\n');
                }}
              >
                +0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X1\nM114\n');
                }}
              >
                +1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 X10\nM114\n');
                }}
              >
                +10
              </Button>
            </ButtonGroup>
          </div>
          <div className="control-moving-sub-container">
            <div className="control-moving-sub-header">
              <h4>Y</h4>
              <input
                readOnly
                value={position.y ? position.y : 0 + 'mm'}
              ></input>
            </div>
            <ButtonGroup aria-label="outlined primary button group">
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y-10\nM114\n');
                }}
              >
                -10
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y-1\nM114\n');
                }}
              >
                -1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y-0.1\nM114\n');
                }}
              >
                -0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G28 Y\nM114\n');
                }}
              >
                <FontAwesomeIcon icon={faHome} />
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y0.1\nM114\n');
                }}
              >
                +0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y1\nM114\n');
                }}
              >
                +1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Y10\nM114\n');
                }}
              >
                +10
              </Button>
            </ButtonGroup>
          </div>
          <div className="control-moving-sub-container">
            <div className="control-moving-sub-header">
              <h4>Z</h4>
              <input
                readOnly
                value={position.z ? position.z : 0 + 'mm'}
              ></input>
            </div>
            <ButtonGroup aria-label="outlined primary button group">
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z-10\nM114\n');
                }}
              >
                -10
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z-1\nM114\n');
                }}
              >
                -1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z-0.1\nM114\n');
                }}
              >
                -0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G28 Z\nM114\n');
                }}
              >
                <FontAwesomeIcon icon={faHome} />
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z0.1\nM114\n');
                }}
              >
                +0.1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z1\nM114\n');
                }}
              >
                +1
              </Button>
              <Button
                size="large"
                onClick={() => {
                  sendGcode('G91\nG0 Z10\nM114\n');
                }}
              >
                +10
              </Button>
            </ButtonGroup>
          </div>
          <Button
            color="secondary"
            onClick={() => {
              sendGcode('M18\nM114\n');
            }}
          >
            Disable Motors
          </Button>
        </div>
      </AccordionDetails>
    </Accordion>
  );
};

export default Moving;
