import React from 'react';
import { Tabs, Tab } from '@material-ui/core';

import './TabList.css';
const TabList = ({ tabList, handleTabClick, activeTab, disabledNav }) => {
  return (
    <Tabs value={activeTab} onChange={handleTabClick} centered>
      {tabList.map((tab) => (
        <Tab
          style={{ height: '70px', fontSize: '20px', minWidth: '140px' }}
          label={tab}
          key={`tab - ${tab}`}
          disabled={disabledNav}
        />
      ))}
    </Tabs>
  );
};

export default TabList;
