import React, { useState } from 'react';
import ComingSoon from '../Common/ComingSoon';
import Moving from '../Control/Moving/Moving';
import { Button, ButtonGroup } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';

const Tune = () => {
  const [offset, setOffset] = useState(0);

  let sendGcode = function (gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        gcode: gcode,
      }),
    });
  };
  let position = { x: 0, y: 0, z: 0 };

  return (
    <div>
      <Button
        onClick={() => {
          sendGcode('M851 Z0.00\nM500\nM501\nG28\nG90\nM211 S0\nG1 Z0\n');
        }}
      >
        Start Z Offset <FontAwesomeIcon />
      </Button>
      <hr />
      <ButtonGroup aria-label="outlined primary button group">
        <Button
          size="large"
          onClick={() => {
            sendGcode('G91\nG0 Z-1\nM114\n');
            setOffset(offset - 1);
            console.log(offset);
          }}
        >
          -1
        </Button>
        <Button
          size="large"
          onClick={() => {
            sendGcode('G91\nG0 Z-0.1\nM114\n');
            setOffset(offset - 0.1);
            console.log(offset);
          }}
        >
          -0.1
        </Button>
        <Button
          size="large"
          onClick={() => {
            sendGcode('G90\nG1 Z0\n');
            setOffset(0);
            console.log(offset);
          }}
        >
          0
        </Button>
        <Button
          size="large"
          onClick={() => {
            sendGcode('G91\nG0 Z0.1\nM114\n');
            setOffset(offset + 0.1);
            console.log(offset);
          }}
        >
          +0.1
        </Button>
        <Button
          size="large"
          onClick={() => {
            sendGcode('G91\nG0 Z1\nM114\n');
            setOffset(offset + 1);
            console.log(offset);
          }}
        >
          +1
        </Button>
      </ButtonGroup>
      <hr />
      <Button
        color="primary"
        variant="contained"
        onClick={() => {
          sendGcode('G90\nM851 Z' + offset + '\nM211 S1\nM500\nM501\nG28\n');
          console.log(offset);
        }}
      >
        Complete Z Offset <FontAwesomeIcon />
      </Button>
    </div>
  );
};

export default Tune;
