import React, { useState } from 'react';

import PrintStatus from './PrintStatus/PrintStatus';
import PrintComplete from './PrintComplete/PrintComplete';

import './Print.css';

const Print = ({ lastMessage, setActiveMode, setDisabledNav }) => {
  let [state, setState] = useState({
    extruder: 0,
    bed: 0,
    connected: false,
    feed: "1",
    file: -1,
    files: [],
    printing: false,
    progress: 0,
    paused: false
  });
  const [file, uploadedFile] = useState(false);
  const [status, setStatus] = useState(0);
  return (
    <div className="print-container">
      {file ? (
        status === 0 ? (
          <PrintStatus lastMessage={lastMessage} setStatus={setStatus} setDisabledNav={setDisabledNav} state={state} />
        ) : (
          <PrintComplete setStatus={setStatus} setActiveMode={setActiveMode} />
        )
      ) : (
        <div>

        <div className="Files">
          <label className="Files-Upload">
            <span>Upload</span>
            <input
              type="file"
              onChange={async e => {
                let data = new FormData();
                console.log(e.target.files);
                e.persist();
                data.append("file", e.target.files[0]);
                // Upload the file
                await fetch(
                  `http://makerhub.local:8080/api/v1/files/${e.target.files[0].name}`,
                  { method: "POST", body: data }
                );
                // Print the file
                await fetch(`http://makerhub.local:8080/api/v1/job/${e.target.files[0].name}`, {
                  method: "POST",
                  headers: { "Content-Type": "application/json" }
                });
                state.printing = true;
                uploadedFile(true)
                setState({
                  files: [...state.files, e.target.files[0].name],
                  file: 0
                });
              }}
            ></input>
          </label>
          </div>
        </div>
      )}
    </div>
  );
};

export default Print;
