import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import OptionCard from '../../Common/OptionCard/OptionCard';
import WifiConnect from './WifiConnect/WifiConnect';
import HotspotConnect from './HotspotConnect/HotspotConnect';

const useStyles = makeStyles({
  header: {
    textAlign: 'center',
    fontSize: '32px',
  },
});
const NetworkType = ({
  setStep,
  setSnackMessage,
  setShowSnack,
  handleSnackClose,
}) => {
  const classes = useStyles();
  const [connectWifi, setConnectWifi] = useState(null);
  const handleClick = (val) => {
    //TODO: ADD LOGIC BASED ON CLICK VAL
    setConnectWifi(() => (val === 'wifi' ? true : false));
  };
  return (
    <div>
      {connectWifi === null && (
        <>
          <h2 className={classes.header}>Network</h2>
          <OptionCard
            title="Connect Wi-Fi"
            onClick={() => handleClick('wifi')}
          />
          <OptionCard
            title="Use Hotspot"
            onClick={() => handleClick('hotspot')}
          />
        </>
      )}
      {connectWifi === true && (
        <WifiConnect
          setStep={setStep}
          setShowSnack={setShowSnack}
          setSnackMessage={setSnackMessage}
          handleSnackClose={handleSnackClose}
          setConnectWifi={setConnectWifi}
        />
      )}
      {connectWifi === false && (
        <HotspotConnect setStep={setStep} setConnectWifi={setConnectWifi} />
      )}
    </div>
  );
};

export default NetworkType;
