import React from 'react';
import Moving from './Moving/Moving';
import Heating from './Heating/Heating';
import Filament from './Filament/Filament';

import './Control.css';

const Control = (lastMessage) => {

  const [bed, setBed] = React.useState(0);
  const [extruder, setExtruder] = React.useState(0);
  const [position, setPosition] = React.useState({ x:0, y:0, z:0 });

  React.useEffect(() => {
    if(
      typeof lastMessage !== 'undefined' &&
      typeof lastMessage.lastMessage !== 'undefined' &&
      lastMessage.lastMessage &&
      typeof lastMessage.lastMessage.data !== 'undefined' &&
      lastMessage.lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.lastMessage.data.substring(1));
      //console.log(json);
      if (json[0].hasOwnProperty('current') && json[0].current.temps[0]) {
        if (json[0].current.temps[0]) {
          let temps = json[0].current.temps[0];
          setExtruder(parseInt(temps.tool0.actual));
          setBed(parseInt(temps.bed.actual));
        }
      }

      if (json[0].hasOwnProperty('event') && json[0].event.hasOwnProperty("payload")) {
        if(json[0].event.payload) {
          setPosition({
            x: json[0].event.payload.x,
            y: json[0].event.payload.y,
            z: json[0].event.payload.z
          });
        }
      }
    }
  }, [lastMessage]);

  return (
    <div>
      <Moving position = {position} />
      <Heating extruder = {extruder} bed = {bed} />
      <Filament />
    </div>
  );
};

export default Control;
