import React, { useState, useCallback, useMemo, useRef } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Setup from './components/Setup/Setup';
import Home from './components/Home/Home';
import {
  createMuiTheme,
  MuiThemeProvider,
  makeStyles,
} from '@material-ui/core/styles';
import { Container } from '@material-ui/core';

import useWebSocket, { ReadyState } from 'react-use-websocket';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: 'rgb(42, 57, 131)',
    },
    secondary: {
      main: '#CD6930',
    },
    type: 'dark',
  },
});
const useStyles = makeStyles({
  container: {
    height: '100vh',
    width: '100vw',
    overflowX: 'hidden',
    padding: 0,
  },
});
const App = () => {
  const classes = useStyles();

  const [mode, setActiveMode] = useState(null);

  const [socketUrl, setSocketUrl] = useState(
    'ws://makerhub.local:9000/sockjs/718/qerksnvm/websocket'
  );
  const messageHistory = useRef([]);

  const { sendMessage, lastMessage, readyState } = useWebSocket(socketUrl, {
    //Will attempt to reconnect on all close events, such as server shutting down
    shouldReconnect: (closeEvent) => true,
    reconnectAttempts: 999999, // default: 20
    reconnectInterval: 0, // default: 5000
    onOpen: () => {
      console.log('socket opened');
    },
    onError: () => {
      console.log('socket error');
    },
    onClose: () => {
      // force reconnect somehow like sending message?
      console.log('socket closed');
    },
  });

  messageHistory.current = useMemo(
    () => messageHistory.current.concat(lastMessage),
    [lastMessage]
  );

  const connectionStatus = {
    [ReadyState.CONNECTING]: 'Connecting',
    [ReadyState.OPEN]: 'Open',
    [ReadyState.CLOSING]: 'Closing',
    [ReadyState.CLOSED]: 'Closed',
    [ReadyState.UNINSTANTIATED]: 'Uninstantiated',
  }[readyState];

  React.useEffect(() => {
    console.log(connectionStatus);
  }, [connectionStatus]);

  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      <Container className={classes.container} fixed>
        {mode === null ? (
          <Setup setActiveMode={setActiveMode} />
        ) : (
          <Home
            // lastMessage={lastMessage}
            mode={mode}
            setActiveMode={setActiveMode}
          />
        )}
      </Container>
    </MuiThemeProvider>
  );
};

export default App;
