import React, { useState, useEffect } from 'react';
import { AppBar } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import TabList from '../Common/Tabs/TabList';
import Print from '../Print/Print';
import Control from '../Control/Control';
import Help from '../Help/Help';
import Tune from '../Tune/Tune';

import './Home.css';
const useStyles = makeStyles((theme) => ({
  appBar: {
    top: 'auto',
    bottom: 0,
  },
}));
const Home = ({ lastMessage, mode, setActiveMode }) => {
  const classes = useStyles();
  const [activeTab, setActiveTab] = useState('Print');
  const [tabList, setTabList] = useState([]);
  const [disabledNav, setDisabledNav] = useState(false);
  useEffect(() => {
    switch (mode) {
      case 0:
        setTabList(['Print', 'Control', 'Menu']);
        break;
      case 1:
        setTabList(['Print', 'Control', 'Tune', 'Menu']);
        break;
      case 2:
        setTabList(['Print', 'Control', 'Tune', 'Menu']);
        break;
      default:
        setTabList(['Print', 'Control', 'Menu']);
    }
  }, [mode]);
  const handleTabClick = (event, val) => {
    setActiveTab(tabList[val]);
  };
  const Components = {
    Print: (
      <Print
        lastMessage={lastMessage}
        setActiveMode={setActiveMode}
        setDisabledNav={setDisabledNav}
      />
    ),
    Control: <Control lastMessage={lastMessage} />,
    Menu: <Help setActiveMode={setActiveMode} />,
    Tune: <Tune />,
  };
  return (
    <div className="home-container">
      <AppBar
        position="fixed"
        color="primary"
        className={classes.appBar}
        disabled={true}
      >
        <TabList
          tabList={tabList}
          handleTabClick={handleTabClick}
          activeTab={tabList.indexOf(activeTab)}
          disabledNav={disabledNav}
        />
      </AppBar>
      <div className="component-container">{Components[activeTab]}</div>
    </div>
  );
};

export default Home;
