import React, { useState, useEffect } from 'react';
import { Button, LinearProgress } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPause, faPlay, faStop } from '@fortawesome/free-solid-svg-icons';

const PrintStatus = ({ lastMessage, setStatus, setDisabledNav, state }) => {
  const [paused, setPaused] = useState(false);
  const [value, setValue] = useState(0);

  let sendGcode = function(gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  }


  useEffect(() => {
    if(
      typeof lastMessage !== 'undefined' &&
      typeof lastMessage.lastMessage !== 'undefined' &&
      lastMessage.lastMessage &&
      typeof lastMessage.lastMessage.data !== 'undefined' &&
      lastMessage.lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.lastMessage.data.substring(1));
      //console.log(json);
      if (json[0].hasOwnProperty('current') && json[0].current.progress) {
        if (value !== json[0].current.progress.completion) {
          setValue(json[0].current.progress.completion);
          setStatus(json[0].current.progress.completion);
        }
      }
    }
  }, [lastMessage]);

  return (
    <>
      <div className="print-btn-container">
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            setPaused((prevState) => !prevState);
            fetch(`http://makerhub.local:8080/api/v1/job`, {
              method: "PATCH",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify({
                file: state.files[state.file]
              })
            });
          }}
        >
          {!paused ? (
            <span>
              <FontAwesomeIcon icon={faPause} /> Pause
            </span>
          ) : (
            <span>
              <FontAwesomeIcon icon={faPlay} /> Play
            </span>
          )}
        </Button>
        <Button
          color="secondary"
          variant="contained"
          onClick={() => {
            setStatus(1);
            sendGcode("M77\n");
          }}
        >
          <span>
            <FontAwesomeIcon icon={faStop} /> Stop
          </span>
        </Button>
      </div>
      <div className="print-status-bar">
        <LinearProgress variant="determinate" value={value} />
      </div>
    </>
  );
};

export default PrintStatus;
